<template>
    <div class="testimonials">
        <div class="testimonials-main">
            <div class="testimonials-title">Testimonials<br />見證分享</div>
            <div class="line"></div>
            <div class="testimonials-ls">
            	<!-- <div class="testimonials-item" v-for="item of testimonialsInfo" :key="item.id">
            		<img class="item-img" :src="item.cover_img">
                    <span class="name">{{item.title}}</span>
                    <div class="testimonials-txt" v-html="item.content"></div>
            	</div> -->
                <waterfall :line-gap="275" :watch="items">
                    <!-- each component is wrapped by a waterfall slot -->
                    <waterfall-slot
                        v-for="(item, index) in items"
                        :width="item.width"
                        :height="item.height"
                        :order="index"
                        :key="item.id"
                    >
                        <!-- <div class="item"></div> -->
                        <div class="testimonials-item">
                            <img class="item-img" :src="item.detail.cover_img">
                            <span class="name">{{item.detail.title}}</span>
                            <div class="testimonials-txt" v-html="item.detail.content"></div>
                        </div>
                    </waterfall-slot>
                </waterfall>
            </div>
            <div class="testimonials-null dn">暂无见证分享</div>
        </div>
    </div>
</template>

<script>
import api from '../api/api';
import Waterfall from 'vue-waterfall/lib/waterfall';
import WaterfallSlot from 'vue-waterfall/lib/waterfall-slot';
import changeLang from '../asset/js/changeLang.js';
let lang = localStorage.getItem('eaestheticLang') || 'zh_hk';

let ItemFactory = (function () {
    let lastIndex = 0;
    function generateRandomItems (data) {
        let items = [];
        let i;
        for (i = 0; i < data.length; i++) {
            items[i] = {
                index: lastIndex++,
                width: 275,
                height: 280 + ~~(Math.random() * 50),
                detail: data[i]
            };
        }
        return items;
    }
    return {
        get: generateRandomItems
    };
})();

export default {
    name: 'testimonials',
    components: {
        Waterfall,
        WaterfallSlot
    },
    data () {
        return {
            testimonialsInfo: '',
            curPage: 1,
            items: [],
            maxPage: 1
        };
    },
    created () {
        document.title = '醫美堂-見證分享';
        // 获取见证分享
        api.getList(2, this.curPage, lang, 12).then((res) => {
            // console.log(res.data.data);
            this.maxPage = res.data.data.last_page;
            this.items = ItemFactory.get(res.data.data.data);
            this.$nextTick(() => {
                changeLang.zhTran(lang);
            });
        });
        window.addEventListener('scroll', this.onScroll);
    },
    methods: {
        onScroll (event) {
            let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
            if ((scrollTop + window.innerHeight >= (document.body.clientHeight - 50)) && this.maxPage > this.curPage) {
                this.curPage++;
                api.getList(2, this.curPage, lang, 12).then((res) => {
                    // console.log(res.data.data);
                    this.items = this.items.concat(ItemFactory.get(res.data.data.data));
                    this.$nextTick(() => {
                        changeLang.zhTran(lang);
                    });
                });
            }
        }
    }
};
</script>

<style lang="less" scoped>
    @import '../asset/less/common.less';
    .vue-waterfall{
        position:relative;
    }
    .vue-waterfall-slot{
        position:absolute;
        margin: 0;
        padding: 0;
        box-sizing:border-box;
    }
    .testimonials{
        width: 100%;
        min-height: 800px;
        background: url(../asset/img/testimonials.jpg) center 0 no-repeat #DFF0F8;
    }
    .testimonials-main{
        position: relative;
        width: @mainWidth;
        margin: 0 auto;
        padding-top: 540px;
    }
    .testimonials-title{
        position: absolute;
        top: 200px;
        width: 280px;
        height: 140px;
        font-size: 48px;
        text-align: right;
        border-bottom: 3px solid #BBD005;
    }
    .line{
        position: absolute;
        top: 337px;
        left: -375px;
        width: 375px;
        height: 3px;
        background-color: #219AD4;
    }
    .testimonials-ls{
        width: 94%;
        min-height: 100px;
        margin: 0 auto;
        padding-bottom: 50px;
        &:after{
        	.clear;
        }
        .testimonials-item{
            position: absolute;
            top: 0;
            left: 0;
            right: 25px;
            bottom: 20px;
            // float: left;
            // width: 250px;
            // padding: 15px 25px;
            // margin-right: 20px;
            text-align: center;
            background-color: #f7f6f6;
            border-radius: 10px;
            img {
                width: 120px;
                height: 120px;
                margin-top: 10px;
                border: 1px solid #fff;
                border-radius: 60px;
                // background: url("../img/loding.gif") no-repeat center;
            }
            .name {
                display: block;
                width: 90%;
                margin: 0 auto;
                font-size: 16px;
                line-height: 40px;
                color: #1D90CE;
                border-bottom: 1px solid #B3C908;
            }
            .testimonials-txt {
                width: 100%;
                min-height: 60px;
                padding: 8px 20px;
                color: #3C5574;
                font-size: 13px;
                line-height: 22px;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 3;
                overflow: hidden;
                p{
                    color: #3C5574;
                    font-size: 14px;
                    line-height: 22px;
                }
            }
        }
    }
    .testimonials-null{
        position: absolute;
        top: 540px;
        left: 30px;
        width: 94%;
        min-height: 100px;
        font-size: 20px;
        .box-start;
        .box-center;
    }
</style>



