<script>
import imgModel from '../common/imgModel.vue';
import api from '../api/api';
import comData from '../common/data.js';
import changeLang from '../asset/js/changeLang.js';
import BMap from 'BMap';

let lang = localStorage.getItem('eaestheticLang') || 'zh_hk';
export default {
    name: 'homePage',
    components: {
        imgModel
    },
    data () {
        return {
            lang: lang,
            address: comData[lang].address,
            contactInfo: {
                name: '',
                phone: '',
                email: '',
                content: ''
            },
            bannerList: '',
            promotionsInfo: [],
            testimonialsInfo: '',
            newsInfo: '',
            promotionsLen: 1,
            curPromotionsPage: 1,
            swiperOption: {
                initialSlide: 1,
                autoplay: 5000,
                direction: 'horizontal',
                pagination: '.swiper-pagination',
                paginationClickable: true,
                loop: true
            },
            swiperOptionTes: {
                direction: 'horizontal',
                pagination: '.swiper-pagination',
                prevButton: '.swiper-button-prev',
                nextButton: '.swiper-button-next',
                slidesPerView: 4,
                // width: '190px',
                spaceBetween: 42
            }
        };
    },
    created () {
        document.title = '醫美堂-首頁';
        // 获取轮播图
        api.getBannerList(lang).then((res) => {
            // console.log(res.data.data);
            this.bannerList = res.data.data.slice(0, 5);
        });
        // 获取最新优惠
        api.getList(1, 1, lang, 12).then((res) => {
            // console.log(res.data.data.data);
            this.promotionsInfo = res.data.data.data;
            this.promotionsLen = Math.ceil(res.data.data.data.length / 6);
        });
        // 获取见证分享
        api.getList(2, 1, lang, 12).then((res) => {
            // console.log(res.data.data);
            this.testimonialsInfo = res.data.data.data;
        });
        api.getList(5, 1, lang, 5).then((res) => {
            // console.log(res.data.data);
            this.newsInfo = res.data.data.data;
        });
    },
    computed: {
        bigImgList () {
            let imgList = [];
            for (let i = 0; i < this.promotionsInfo.length; i++) {
                let startP = this.getPlace(this.promotionsInfo[i].content, 'src=', 0);
                let endP = this.getPlace(this.promotionsInfo[i].content, '\" ', 0);
                let imgSrc = this.promotionsInfo[i].content.substr(startP + 5, endP - 13);
                console.log(imgSrc);
                imgList.push(imgSrc);
            }
            return imgList;
        }
    },
    mounted () {
        let that = this;
        // 百度地图API功能
        let map = new BMap.Map('map');
        map.centerAndZoom(new BMap.Point(114.186196, 22.300973), 19);
        map.addControl(new BMap.MapTypeControl());
        map.setCurrentCity('香港');
        map.enableScrollWheelZoom(true);
        let marker = new BMap.Marker(new BMap.Point(114.186196, 22.300973));
        map.addOverlay(marker);
        that.$nextTick(() => {
            changeLang.zhTran(that.lang);
        });
    },
    methods: {
        showBigImg (src, index) {
            this.$store.commit({
                type: 'changeBigImg',
                src: this.bigImgList,
                index: index,
                len: this.promotionsInfo.length
            });
        },
        getPlace (str, cha, num) {
            let x = str.indexOf(cha);
            for (let i = 0; i < num; i++) {
                x = str.indexOf(cha, x + 1);
            }
            return x;
        },
        turnLeftP () {
            let promotionsUl = document.querySelector('.promotions-ul');
            promotionsUl.style.transform = 'translate3d(0,0,0)';
            promotionsUl.style.transition = 'all .5s linear';
            this.curPromotionsPage = 1;
        },
        turnRightP () {
            let promotionsUl = document.querySelector('.promotions-ul');
            promotionsUl.style.transform = 'translate3d(-850px,0,0)';
            promotionsUl.style.transition = 'all .5s linear';
            this.curPromotionsPage = 2;
        },
        toNewsDetail (id) {
            this.$router.push({path: '/pc/newsDetail?id=' + id});
        },
        contaceUs () {
            let regPhone = /^1[3|4|5|8|7][0-9]\d{4,8}$/;
            let regEmail = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
            if (this.contactInfo.name === '') {
                alert('稱呼不能为空');
                return;
            } else if (this.contactInfo.phone === '') {
                alert('请输入电话');
                return;
            } else if (this.contactInfo.email === '') {
                alert('请输入电邮');
                return;
            } else if (this.contactInfo.phone === '') {
                alert('请输入正文');
                return;
            } else if (!regEmail.test(this.contactInfo.email)) {
                alert('请输入正确的电邮');
                return;
            }
            // console.log(this.contactInfo);
            let str = '称呼：' + this.contactInfo.name + ';<br />';
            str += '电话：' + this.contactInfo.phone + ';<br />';
            str += '电邮：' + this.contactInfo.email + ';<br />';
            str += '正文：' + this.contactInfo.content;
            window.location = 'mailto:askme@eaesthetic.com.hk?body=' + str;
            this.contactInfo.name = '';
            this.contactInfo.phone = '';
            this.contactInfo.email = '';
            this.contactInfo.content = '';
        }
    }
};
</script>

