                background-color: #219AD4;
            }
            .iconfont{
                font-size: 20px;
            }
        }
    }
    .aboutUs{
        width: 100%;
        height: 530px;
        background-color: #E0F0F9;
        &:after{
            .clear;
        }
        .aboutUs-left{
            float: left;
            width: 50%;
            height: 100%;
            .map{
                width: 100%;
                height: 100%;
            }
        }
        .aboutUs-right{
            float: left;
            width: 50%;
            height: 100%;;
            .module-title{
                width: 90%;
                height: 100px;
                margin-left: 10%;
                margin-top: 40px;
                border-bottom: 3px solid #4BC6E0;
                h2{
                    letter-spacing: 3px;
                    text-align: left;
                }
            }
            .contact-info{
                position: relative;
                width: 400px;
                height: 280px;
                margin-left: 10%;
                margin-top: 30px;
                input{
                    margin-bottom: 15px;
                    text-indent: 8px;
                }
                .name, .phone{
                    display: inline-block;
                    width: 180px;
                    height: 30px;
                    border: 1px solid #419BF9;
                    background: none;
                }
                .phone{
                    margin-left: 30px;
                }
                .email{
                    width: 395px;
                    height: 30px;
                    border: 1px solid #419BF9;
                    background: none;
                }
                .contact-txt{
                    width: 395px;
                    height: 125px;
                    line-height: 25px;
                    text-indent: 8px;
                    resize: none;
                    border: 1px solid #419BF9;
                    background: none;
                }
                :-moz-placeholder { /* Mozilla Firefox 4 to 18 */
                    color: #1e8fce;
                    opacity:.5;
                }
                ::-moz-placeholder { /* Mozilla Firefox 19+ */
                    color: #1e8fce;
                    opacity:.5;
                }
                input:-ms-input-placeholder{
                    color: #1e8fce;
                    opacity:.5;
                }
                input::-webkit-input-placeholder{
                    color: #1e8fce;
                    opacity:.5;
                }
                textarea:-ms-input-placeholder{
                    color: #1e8fce;
                    opacity:.5;
                }
                textarea::-webkit-input-placeholder{
                    color: #1e8fce;
                    opacity:.5;
                }
                .btn-send{
                    position: absolute;
                    bottom: 20px;
                    right: 0;
                    width: 110px;
                    height: 30px;
                    line-height: 30px;
                    text-align: center;
                    color: #FFF;
                    cursor: pointer;
                    background-color: #BBD005;
                    border-radius: 5px;
                }
            }
        }
    }
</style>



