<template>
    <div class="img-model" v-show="$store.state.showBigImg">
        {{$store.state.imgIndex}}
        {{$store.state.imgLen}}
        <div class="model-content">
            <span class="iconfont icon-close btn-close" @click="closeModel"></span>
            <img :src="$store.state.imgSrc">
            <span class="iconfont icon-arrow-left btn-bigImg-left" v-show="$store.state.imgIndex !== 0 && $store.state.imgIndex > 0" @click="turnLeft"></span>
            <span class="iconfont icon-arrow-right btn-bigImg-right" v-show="$store.state.imgIndex < $store.state.imgLen - 1"@click="turnRight"></span>
        </div>
    </div>
</template>

<script>
export default {
    name: 'img-model',
    methods: {
        closeModel () {
            this.$store.commit({
                type: 'changeBigImg',
                src: '../asset/img/pixel.gif'
            });
        },
        turnLeft () {
            this.$store.commit({
                type: 'changeSrc',
                index: this.$store.state.imgIndex - 1
            });
        },
        turnRight () {
            this.$store.commit({
                type: 'changeSrc',
                index: this.$store.state.imgIndex + 1
            });
        }
    }
};
</script>

<style lang="less" scoped>
@import '../asset/less/common.less';
.img-model{
    z-index: 10;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,.7);
    .box-start;
    .box-center;
}
.model-content{
    position: relative;
    width: 500px;
    max-height: 80%;
    min-width: 300px;
    min-height: 300px;
    padding: 20px 20px 0 20px;
    overflow-y: auto;
    overflow-x: hidden;
    border-radius: 8px;
    background: url(../asset/img/loading.gif) center center no-repeat rgba(255,255,255,.8);
    .box-start;
    .box-center;
    .box-orient(vertical);
    &::-webkit-scrollbar{
        width: 0;
    }
    .btn-close{
        position: absolute;
        right: 3px;
        top: 3px;
        cursor: pointer;
        font-size: 24px;
        &:hover{
            color: #2E8DED;
        }
    }
    img{
        display: block;
        max-width: 100%;
        margin: 0 auto;
    }
    .btn-bigImg-left{
        display: block;
        position: fixed;
        left: 70px;
        top: 46%;
        width: 60px;
        height: 60px;
        font-size: 44px;
        color: #219AD4;
        cursor: pointer;
    }
    .btn-bigImg-right{
        display: block;
        position: fixed;
        right: 50px;
        top: 46%;
        width: 60px;
        height: 60px;
        font-size: 44px;
        color: #219AD4;
        cursor: pointer;
    }
}
</style>



