import Vue from 'vue';
import App from './App';
import store from './store';
import router from './router';
import VueAwesomeSwiper from 'vue-awesome-swiper';

Vue.config.productionTip = true;
Vue.use(VueAwesomeSwiper);
/* eslint-disable no-new */
new Vue({
    el: '#app',
    store,
    router,
    template: '<App/>',
    components: { App }
});



