<template>
    <header class="header">
        <section class="main-header">
            <div class="logo"></div>
            <div class="nav">
                <!-- <router-link class="nav-item" to="/pc/homePage" exact>醫學美容</router-link>
                <router-link class="nav-item" to="/pc/homePage#promotions">最新優惠</router-link>
                <router-link class="nav-item" to="/pc/testimonials">見證分享</router-link>
                <router-link class="nav-item" to="/pc/facilities">專業設施</router-link>
                <router-link class="nav-item" to="/pc/services">信心服務</router-link>
                <router-link class="nav-item" to="/pc/news">集團資訊</router-link>
                <router-link class="nav-item" to="/pc/homePage#aboutUs">聯絡我們</router-link> -->
                <router-link class="nav-item" v-for="(item, index) of navList" :key="index" :to="item.href" :exact="index == 0" v-html="item.title"></router-link>
            </div>
            <div class="other-frame">
                <div class="share-frame">
                    <a href="https://www.facebook.com/EAesthetic-%E9%86%AB%E7%BE%8E%E5%A0%82-287246328394575/" target="_blank">
                        <span class="iconfont icon-facebook"></span>
                    </a>
                    <a @click="closeModel">
                        <span class="iconfont icon-wechat"></span>
                    </a>
                    <a href="#">
                        <span class="iconfont icon-twitter"></span>
                    </a>
                    <a href="#">
                        <span class="iconfont icon-youtube"></span>
                    </a>
                    <a href="#">
                        <span class="iconfont icon-google"></span>
                    </a>
                    <a href="#">
                        <span class="iconfont icon-instagram"></span>
                    </a>
                </div>
                <select class="language" v-model="language" @change="changeLanguage">
                    <option value="zh_hk">中文繁体</option>
                    <option value="zh_cn">中文简体</option>
                    <option value="en">English</option>
                </select>
            </div>
        </section>
    </header>
</template>

<script>
import comData from '../common/data.js';
import changeLang from '../asset/js/changeLang.js';
let lang = localStorage.getItem('eaestheticLang') || 'zh_hk';
export default {
    name: 'myHeader',
    data () {
        return {
            language: lang,
            navList: []
        };
    },
    created () {
        if (localStorage.getItem('eaestheticLang')) {
            this.language = localStorage.getItem('eaestheticLang');
        } else {
            localStorage.eaestheticLang = this.language;
        }
        this.navList = comData[this.language].navInfo;
        changeLang.zhTran(this.language);
        // api.getNavList(lang).then((res) => {
        //     console.log(res.data.data.categoryList);
        // });
    },
    methods: {
        changeLanguage () {
            localStorage.eaestheticLang = this.language;
            location.reload();
        },
        closeModel () {
            this.$store.commit('changeModelState');
        }
    }
};
</script>

<style lang="less">
    @import '../asset/less/common.less';
    .header{
        z-index: 9;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 110px;
        background-color: rgba(70,156,197,.8);
        .main-header{
            width: @mainWidth;
            height: 100%;
            margin: 0 auto;
            .box-start;
            .box-align(center);
            .box-pack(justify);
        }
        .logo{
            width: 155px;
            height: 80px;
            margin-left: 10px;
            background: url(../asset/img/logo.png) no-repeat;
            background-size: 100%;
        }
        .nav{
            width: 700px;
            height: 100%;
            .box-start;
            .box-center;
            .nav-item{
                width: 100px;
                height: 100%;
                color: #FFF;
                text-align: center;
                border-top: 3px solid transparent;
                .box-start;
                .box-center;
                &:hover, &.active{
                    border-top: 3px solid #bbd005;
                }
            }
        }
        .other-frame{
            width: 180px;
            height: 100%;
            margin-right: 20px;
            .box-start;
            .box-center;
            .box-orient(vertical);
            .share-frame{
                width: 100%;
                height: 50px;
                .box-start;
                .box-center;
                a{
                    display: block;
                    margin-right: 5px;
                    color: #FFF;
                    cursor: pointer;
                    font-size: 24px;
                    span{
                        font-size: 24px;
                    }
                }
            }
            .language{
                width: 90px;
                height: 30px;
                padding-left: 10px;
                color: #FFF;;
                cursor: pointer;
                border: none;
                border-radius: 3px;
                background-color: rgba(80,162,203,1);
                option{
                    width: 100%;
                    color: #333;
                    background-color: #FFF;
                }
            }
        }
    }
</style>



