<template>
    <footer class="footer">
        <section class="main-footer">
            <div class="footer-item footer-logo"></div>
            <div class="footer-item footer-intro">
                <p v-for="(item, index) of footerInfo" :key="index">{{item}}</p>
                <!-- <p>我們專業的顧問團隊承諾.</p>
                <p>以專業的知識及以心致誠的服務態度.</p>
                <p>期望為您提供最貼心的專業服務.</p>
                <p>歡迎與我們聯絡.樂意隨時為您效勞.</p> -->
            </div>
            <div class="footer-item subscription">
                <p class="big-font">SUBSCRIPTION</p>
                <p>輸入您的電郵地址</p>
                <p>獲得更多醫學美容資訊</p>
                <div class="email-frame">
                    <input type="email" placeholder="Email Address" v-model="email">
                    <span class="iconfontfont icon-arrow-right1" @click="send"></span>
                </div>
            </div>
            <div class="footer-item footer-other">
                <p class="big-font">CONTACT US</p>
                <p>{{address}}</p>
                <p>
                    <span>Tel:2983 8388</span>
                    <span>Fax:2361 8800</span>
                </p>
                <div class="share-frame">
                    <a href="https://www.facebook.com/EAesthetic-%E9%86%AB%E7%BE%8E%E5%A0%82-287246328394575/" target="_blank">
                        <span class="iconfont icon-facebook"></span>
                    </a>
                    <a @click="closeModel">
                        <span class="iconfont icon-wechat"></span>
                    </a>
                    <a href="#">
                        <span class="iconfont icon-twitter"></span>
                    </a>
                    <a href="#">
                        <span class="iconfont icon-youtube"></span>
                    </a>
                    <a href="#">
                        <span class="iconfont icon-google"></span>
                    </a>
                    <a href="#">
                        <span class="iconfont icon-instagram"></span>
                    </a>
                </div>
            </div>
        </section>
        <p class="copyright">Privacy Policy | Copyright © 2017   eAesthetic Holdings limited. All rights reserved.</p>
    </footer>
</template>

<script>
import comData from '../common/data.js';
let lang = localStorage.getItem('eaestheticLang') || 'zh_cn';
export default {
    name: 'myFooter',
    data () {
        return {
            email: '',
            footerInfo: comData[lang].footerInfo,
            address: comData[lang].address
        };
    },
    methods: {
        send () {
            window.location = 'mailto:subcriptme@eaesthetic.com.hk?subject=訂閱更多醫學美容資訊&body=' + this.email;
            this.email = '';
        },
        closeModel () {
            this.$store.commit('changeModelState');
        }
    }
};
</script>

<style lang="less">
    @import '../asset/less/common.less';
    .footer{
        width: 100%;
        height: 260px;
        background: url(../asset/img/footer_bg.jpg) center 0 no-repeat #1e8fce;
        .main-footer{
            width: @mainWidth;
            height: 210px;
            margin: 0 auto;
            color: #FFF;
            font-size: 14px;
            .box-start;
            .box-center;
            .footer-item{
                width: 300px;
                height: 160px;
                .big-font{
                    font-size: 20px;
                }
                p{
                    font-size: 13px;
                    letter-spacing: 1px;
                    line-height: 28px;
                }
            }
            .footer-logo{
                width: 220px;
                background: url(../asset/img/logo.png) 0 center no-repeat;
            }
            .footer-intro{
                width: 360px;
                .box-start;
                .box-pack(center);
                .box-orient(vertical);
            }
            .subscription{
                width: 260px;
                .box-start;
                .box-pack(center);
                .box-orient(vertical);
                .email-frame{
                    position: relative;
                    width: 200px;
                    height: 30px;
                    margin-top: 8px;
                    border: 1px solid #FFF;
                    border-radius: 3px;
                    input{
                        display: block;
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 170px;
                        height: 30px;
                        color: #FFF;
                        text-indent: 8px;
                        border: none;
                        background: none;
                    }
                    span{
                        display: block;
                        position: absolute;
                        top: 2px;
                        right: 2px;
                        width: 24px;
                        height: 24px;
                        cursor: pointer;
                        font-size: 20px;
                    }
                }
            }
            .footer-other{
                width: 240px;
                .box-start;
                .box-pack(center);
                .box-align(end);
                .box-orient(vertical);
                .share-frame{
                    width: 100%;
                    height: 40px;
                    .box-start;
                    .box-pack(end);
                    a{
                        display: block;
                        margin-right: 5px;
                        color: #FFF;
                        font-size: 24px;
                        cursor: pointer;
                        span{
                            font-size: 24px;
                        }
                    }
                }
            }
        }
        .copyright{
            width: 100%;
            height: 50px;
            line-height: 50px;
            font-size: 12px;
            color: #FFF;
            text-align: center;
        }
    }
</style>



