<template>
    <div class="servicesDetail">
        <div class="servicesDetail-main">
            <div class="services-title">Services<br />信心服務</div>
            <div class="line"></div>
            <div class="servicesDetail-frame">
                <h2>{{servicesDetail.title}}</h2>
                <div class="servicesDetail-content" v-html="servicesDetail.content"></div>
                <div class="btn-back" @click="toBack">
                    <span class="iconfont icon-arrow-left1"></span>
                    <span>{{lang == 'en' ? 'Back': '返回列表'}}</span>
                </div>
            </div>
        </div>
        <div class="services-other">
            <p class="services-other-title">醫學儀器療程</p>
            <div class="services-ls-frame">
                <swiper :options="swiperOptionSer" class="services-list">
                    <swiper-slide class="services-item" v-for="item of serviceslist" :key="item.id">
                        <img :src="item.cover_img" @click="toServicesDetail(item.id)">
                        <span @click="toServicesDetail(item.id)">{{item.title}}</span>
                    </swiper-slide>
                    <div class="swiper-button-prev" slot="button-prev"></div>
                    <div class="swiper-button-next" slot="button-next"></div>
                </swiper>
            </div>
        </div>
    </div>
</template>

<script>
import api from '../api/api';
import changeLang from '../asset/js/changeLang.js';
let lang = localStorage.getItem('eaestheticLang') || 'zh_hk';
export default {
    name: 'servicesDetail',
    data () {
        return {
            lang: lang,
            serviceslist: '',
            servicesDetail: '',
            swiperOptionSer: {
                initialSlide: 0,
                direction: 'horizontal',
                prevButton: '.swiper-button-prev',
                nextButton: '.swiper-button-next',
                slidesPerView: 4,
                spaceBetween: 40
            }
        };
    },
    created () {
        document.title = '醫美堂-信心服務';
        api.getArticleDetail(this.$route.query.id, lang).then((res) => {
            this.servicesDetail = res.data.data[0];
            // console.log(res.data.data);
        });
        api.getList(this.$route.query.curId, 1, lang, 12).then((res) => {
            // console.log(res.data.data);
            this.serviceslist = res.data.data.data;
        });
    },
    mounted () {
        this.$nextTick(() => {
            changeLang.zhTran(lang);
        });
    },
    methods: {
        toServicesDetail (id) {
            api.getArticleDetail(id, lang).then((res) => {
                this.servicesDetail = res.data.data[0];
                document.documentElement.scrollTop = document.body.scrollTop = 0;
            });
        },
        toBack () {
            this.$router.push({ path: '/pc/services' });
        }
    }
};
</script>

<style lang="less" scoped>
    @import '../asset/less/common.less';
    .servicesDetail{
        width: 100%;
        min-height: 800px;
        background: url(../asset/img/services.jpg) center 0 no-repeat #DFF0F8;
    }
    .servicesDetail-main{
        position: relative;
        width: @mainWidth;
        margin: 0 auto;
        padding-top: 540px;
    }
    .services-title{
        position: absolute;
        top: 200px;
        width: 280px;
        height: 140px;
        font-size: 48px;
        text-align: right;
        border-bottom: 3px solid #BBD005;
    }
    .line{
        position: absolute;
        top: 337px;
        left: -375px;
        width: 375px;
        height: 3px;
        background-color: #219AD4;
    }
    .servicesDetail-frame{
        width: 94%;
        min-height: 100px;
        margin: 0 auto;
        padding-bottom: 40px;
        text-align: center;
        .servicesDetail-content{
            padding: 20px 90px;
            font-size: 14px;
            line-height: 25px;
            color: #061141;
            p{
                max-width: 100%;
                text-indent: 28px;
                text-align: left;
            }
            img{
                display: block;
                width: 100%;
                margin: 0 auto;
            }
        }
        .btn-back{
            width: 200px;
            height: 40px;
            margin: 30px auto;
            line-height: 40px;
            text-align: center;
            font-size: 18px;
            cursor: pointer;
            color: #219AD4;
            border: 1px solid #219AD4;/*no*/
            border-radius: 5px;
            .iconfont{
                font-size: 18px;
            }
            &:hover{
                color: #FFF;
                background-color: #219AD4;
            }
        }
    }
    .services-other{
        position: relative;
        width: 1050px;
        margin: 0 auto;
        padding-bottom: 80px;
        overflow: hidden;
        .services-other-title{
            padding-left: 40px;
        }
        .services-ls-frame{
            position: relative;
            width: 1050px;
            height: 200px;
            margin-top: 20px;
            overflow: hidden;
            .services-list{
                width: 1050px;
                height: 100%;
                padding-left: 40px;
                padding-right: 40px;
                .services-item{
                    float: left;
                    width: 210px;
                    height: 200px;
                    margin-right: 30px;
                    cursor: pointer;
                    img{
                        width: 100%;
                        height: 158px;
                    }
                    span{
                        display: block;
                        margin-top: 15px;
                        color: #333;
                        font-size: 14px;
                        text-align: center;
                    }
                    &:hover{
                        img{
                            opacity: .85;
                        }
                        span{
                            color: #000;
                        }
                    }
                }
                .swiper-button-disabled{
                    opacity: 0;
                }
                .swiper-button-prev{
                    position: absolute;
                    top: 60px;
                    left: 0;
                    cursor: pointer;
                }
                .swiper-button-next{
                    position: absolute;
                    top: 60px;
                    right: 0;
                    cursor: pointer;
                }
            }
            
        }
    }
</style>



