<template>
    <div class="newsDetail">
        <div class="newsDetail-main">
            <div class="newsDetail-title">News<br />集團資訊</div>
            <div class="line"></div>
            <div class="newsDetail-content">
                <h2>{{newsDetail.title}}</h2>
                <p class="newsDetail-time" v-if="newsDetail.created_at">{{newsDetail.created_at.slice(0, 10)}}</p>
                <div class="newsDetail-txt" v-html="newsDetail.content"></div>
                <div class="btn-back" @click="toBack">
                    <span class="iconfont icon-arrow-left1"></span>
                    <span>{{lang == 'en' ? 'Back': '返回列表'}}</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import api from '../api/api';
import changeLang from '../asset/js/changeLang.js';
let lang = localStorage.getItem('eaestheticLang') || 'zh_hk';
export default {
    name: 'newsDetail',
    data () {
        return {
            newsDetail: '',
            lang: lang
        };
    },
    created () {
        document.title = '醫美堂-集團資訊';
        api.getArticleDetail(this.$route.query.id, lang).then((res) => {
            // console.log(res.data.data);
            this.newsDetail = res.data.data[0];
            this.$nextTick(() => {
                changeLang.zhTran(lang);
            });
        });
    },
    methods: {
        toBack () {
            this.$router.go(-1);
        }
    }
};
</script>

<style lang="less" scoped>
    @import '../asset/less/common.less';
    .newsDetail{
        width: 100%;
        min-height: 800px;
        background: url(../asset/img/news.jpg) center 0 no-repeat #F8F6F7;
    }
    .newsDetail-main{
        position: relative;
        width: @mainWidth;
        margin: 0 auto;
        padding-top: 540px;
        padding-bottom: 50px;
    }
    .newsDetail-title{
        position: absolute;
        top: 200px;
        width: 280px;
        height: 140px;
        font-size: 48px;
        text-align: right;
        border-bottom: 3px solid #BBD005;
    }
    .line{
        position: absolute;
        top: 337px;
        left: -375px;
        width: 375px;
        height: 3px;
        background-color: #219AD4;
    }
    .newsDetail-content{
        width: 94%;
        min-height: 100px;
        margin: 0 auto;
        h2{
            line-height: 60px;
            font-size: 30px;
        }
        .newsDetail-time{
            font-size: 14px;
            color: #219AD4;
        }
        .newsDetail-txt{
            width: 100%;
            margin-top: 30px;
            p{
                font-size: 16 px;
                line-height: 28px;
            }
        }
    }
    .btn-back{
        width: 200px;
        height: 40px;
        margin: 30px auto 0;
        line-height: 40px;
        text-align: center;
        font-size: 18px;
        cursor: pointer;
        color: #219AD4;
        border: 1px solid #219AD4;
        border-radius: 5px;
        .iconfont{
            font-size: 20px;
        }
        &:hover{
            color: #FFF;
            background-color: #219AD4;
        }
    }
</style>



