<template>
    <div class="model" v-show="$store.state.isShow">
        <div class="model-content">
            <span class="iconfont icon-close btn-close" @click="closeModel"></span>
            <img src="../asset/img/two-code.png">
            <p class="hint">
                <span class="iconfont icon-sys"></span>
                <span>微信掃一掃</span>
            </p>
        </div>
    </div>
</template>

<script>
export default {
    name: 'model',
    methods: {
        closeModel () {
            this.$store.commit('changeModelState');
        }
    }
};
</script>

<style lang="less" scoped>
@import '../asset/less/common.less';
.model{
    z-index: 10;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,.7);
    .box-start;
    .box-center;
}
.model-content{
    position: relative;
    width: 360px;
    height: 360px;
    padding-top: 40px;
    border-radius: 8px;
    background-color: rgba(255,255,255,.8);
    .box-start;
    .box-center;
    .box-orient(vertical);
    .btn-close{
        position: absolute;
        right: 3px;
        top: 3px;
        cursor: pointer;
        font-size: 24px;
        &:hover{
            color: #2E8DED;
        }
    }
    img{
        display: blcok;
        width: 280px;
        height: 280px;
    }
    .hint{
        width: 100%;
        height: 30px;
        line-height: 30px;
        text-align: center;
        span{
            font-size: 18px;
            &:first-child{
                margin-right: 5px;
                font-size: 20px;
                color: #000;
                font-weight: bold;
            }
        }
    }
}
</style>



