import axios from 'axios';

const reqUrl = 'http://www.eclinix.com.hk:5100';

const api = {
    getBannerList (lang) {
        return axios.get(reqUrl + '/carousels', {
            params: {
                lang: lang
            }
        });
    },
    // 获取列表
    getList (category, page, lang, pagesize) {
        return axios.get(reqUrl + '/posts-list', {
            params: {
                page: page,
                pagesize: pagesize || 6,
                category: category,
                lang: lang
            }
        });
    },
    // 获取文章详情
    getArticleDetail (id, lang) {
        return axios.get(reqUrl + '/posts', {
            params: {
                id: id,
                lang: lang
            }
        });
    },
    // 获取信心服務列表
    servicesNavlist (lang) {
        return axios.get(reqUrl + '/sub-categories', {
            params: {
                lang: lang,
                pid: 4
            }
        });
    }
};

export default api;



