import Vue from 'vue';
import Router from 'vue-router';
import homePage from '@/homePage';
import facilities from '@/facilities';
import testimonials from '@/testimonials';
import services from '@/services';
import news from '@/news';
import newsDetail from '@/newsDetail';
import servicesDetail from '@/servicesDetail';

Vue.use(Router);

export default new Router({
	mode: 'history',
	linkActiveClass: 'active',
    routes: [
		{
            path: '/pc/',
            redirect: '/pc/homePage'
        },
        {
      		path: '/pc/homePage',
      		name: 'homePage',
      		component: homePage,
      		meta: {
	            title: '醫美堂-首頁'
	        }
		},
		{
			path: '/pc/testimonials',
			name: 'testimonials',
			component: testimonials,
      		meta: {
	            title: '醫美堂-見證分享'
	        }
		},
		{
			path: '/pc/facilities',
			name: 'facilities',
			component: facilities,
      		meta: {
	            title: '醫美堂-專業設施'
	        }
		},
		{
			path: '/pc/services',
			name: 'services',
			component: services,
      		meta: {
	            title: '醫美堂-信心服務'
	        }
		},
		{
			path: '/pc/news',
			name: 'news',
			component: news,
      		meta: {
	            title: '醫美堂-集團資訊'
	        }
		},
		{
			path: '/pc/newsDetail',
			name: 'newsDetail',
			component: newsDetail,
      		meta: {
	            title: '醫美堂-集團資訊'
	        }
		},
		{
			path: '/pc/servicesDetail',
			name: 'servicesDetail',
			component: servicesDetail,
      		meta: {
	            title: '醫美堂-信心服務'
	        }
		}
  	],
  	scrollBehavior (to, from, savedPosition) {
	 	if (to.hash) {
	 		return {
	 			selector: to.hash
	 		};
	 	} else if (savedPosition) {
		    return savedPosition;
		} else {
		    return { x: 0, y: 0 };
		}
	}
});



