<template>
    <div class="news">
        <div class="news-main">
            <div class="news-title">News<br />集團資訊</div>
            <div class="line"></div>
            <div class="news-ls">
                <div class="news-item" v-for="item of newsInfo" :key="item.id" @click="toNewsDetail(item.id)">
                    <img :src="item.cover_img">
                    <div class="news-item-content">
                        <h3>{{item.title}}</h3>
                        <p class="news-item-time">{{item.created_at.slice(0, 10)}}</p>
                        <p class="news-item-info">{{item.preface}}</p>
                    </div>
                    <span class="btn-read">{{lang == 'en' ? 'Read': '閱讀'}}</span>
                </div>
                <div class="btn-loadmore" v-if="curPage < maxPage" @click="loadMore">
                    <span class="iconfont icon-add"></span>
                    <span>{{lang == 'en' ? 'More': '加載更多'}}</span>
                </div>
            </div>
            <div class="news-null dn">暂无集團資訊</div>
        </div>
    </div>
</template>

<script>
import api from '../api/api';
import changeLang from '../asset/js/changeLang.js';
let lang = localStorage.getItem('eaestheticLang') || 'zh_hk';
export default {
    name: 'news',
    data () {
        return {
            lang: lang,
            newsInfo: [],
            curPage: 1,
            maxPage: 0
        };
    },
    created () {
        document.title = '醫美堂-集團資訊';
        api.getList(5, this.curPage, lang, 10).then((res) => {
            // console.log(res.data.data);
            this.newsInfo = res.data.data.data;
            this.maxPage = res.data.data.last_page;
            this.$nextTick(() => {
                changeLang.zhTran(lang);
            });
        });
    },
    methods: {
        loadMore () {
            this.curPage++;
            api.getList(5, this.curPage, lang, 10).then((res) => {
                // console.log(res.data.data);
                this.newsInfo = this.newsInfo.concat(res.data.data.data);
                this.$nextTick(() => {
                    changeLang.zhTran(lang);
                });
            });
        },
        toNewsDetail (id) {
            this.$router.push({path: '/pc/newsDetail?id=' + id});
        }
    }
};
</script>

<style lang="less" scoped>
    @import '../asset/less/common.less';
    .news{
        width: 100%;
        min-height: 800px;
        background: url(../asset/img/news.jpg) center 0 no-repeat #F8F6F7;
    }
    .news-main{
        position: relative;
        width: @mainWidth;
        margin: 0 auto;
        padding-top: 540px;
        padding-bottom: 50px;
    }
    .news-title{
        position: absolute;
        top: 200px;
        width: 280px;
        height: 140px;
        font-size: 48px;
        text-align: right;
        border-bottom: 3px solid #BBD005;
    }
    .line{
        position: absolute;
        top: 337px;
        left: -375px;
        width: 375px;
        height: 3px;
        background-color: #219AD4;
    }
    .news-ls{
        width: 94%;
        min-height: 100px;
    }
    .news-item{
        width: 94%;
        height: 200px;
        margin: 0 auto;
        padding-top: 25px;
        padding-bottom: 25px;
        cursor: pointer;
        border-bottom: 1px solid #DCDCDC;
        .box-start;
        .box-center;
        img{
            display: block;
            width: 210px;
            height: 100%;
        }
        .news-item-content{
            width: 570px;
            height: 100%;
            margin-left: 50px;
            h3{
                width: 100%;
                font-size: 20px;
                line-height: 40px;
                display: block;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
            }
            .news-item-time{
                font-size: 12px;
                line-height: 28px;
                color: #219AD4;
            }
            .news-item-info{
                font-size: 14px;
                line-height: 28px;
            }
        }
        .btn-read{
            display: block;
            width: 120px;
            height: 40px;
            margin-left: 30px;
            line-height: 40px;
            text-align: center;
            color: #219AD4;
            font-size: 18px;
            border: 1px solid #219AD4;
            border-radius: 5px;
            &:hover{
                color: #FFF;
                background-color: #219AD4;
            }
        }
    }
    .btn-loadmore{
        width: 200px;
        height: 40px;
        margin: 30px auto 0;
        line-height: 40px;
        text-align: center;
        font-size: 18px;
        cursor: pointer;
        color: #219AD4;
        border: 1px solid #219AD4;
        border-radius: 5px;
        .iconfont{
            font-size: 20px;
        }
        &:hover{
            color: #FFF;
            background-color: #219AD4;
        }
    }
    .news-null{
        position: absolute;
        top: 540px;
        left: 30px;
        width: 94%;
        min-height: 100px;
        font-size: 20px;
        .box-start;
        .box-center;
    }
</style>



