<template>
    <div class="services">
        <div class="services-main">
            <div class="services-title">Services<br />信心服務</div>
            <div class="line"></div>
            <div class="services-frame">
                <ul class="services-nav">
                    <li :class="item.id == curId ? 'active' : ''" v-for="item of servicesNavlist" :key="item.id" @click="getServiceslist(item.id)">{{item.name}}</li>
                </ul>
                <ul class="services-list">
                    <li class="services-item" v-for="item of serviceslist" :key="item.id" @click="toServicesDetail(item.id, curId)">
                        <img :src="item.cover_img">
                        <span class="item-title">{{item.title}}</span>
                    </li>
                </ul>
                <div class="btn-more-services" @click="loadMore" v-if="curPage < maxPage">
                    <i class="iconfont icon-add"></i>
                    <span>{{lang == 'en' ? 'More' : '發掘更多'}}</span>
                </div>
            </div>
            <div class="services-null dn">暂无信心服務</div>
        </div>
    </div>
</template>

<script>
import api from '../api/api';
import changeLang from '../asset/js/changeLang.js';
let lang = localStorage.getItem('eaestheticLang') || 'zh_hk';
export default {
    name: 'services',
    data () {
        return {
            lang: lang,
            serviceslist: '',
            servicesNavlist: '',
            curId: 0,
            curPage: 1,
            maxPage: 1
        };
    },
    created () {
        document.title = '醫美堂-信心服務';
        let that = this;
        api.servicesNavlist(lang).then((res) => {
            // console.log(res.data.data);
            this.servicesNavlist = res.data.data;
            that.$nextTick(() => {
                that.getServiceslist(this.servicesNavlist[0].id);
            });
        });
    },
    methods: {
        toServicesDetail (id, curId) {
            this.$router.push({
                path: '/pc/servicesDetail',
                query: { id: id, curId: curId }
            });
        },
        getServiceslist (id) {
            let that = this;
            if (that.curId !== id) {
                that.curId = id;
                that.curPage = 1;
                api.getList(id, that.curPage, lang, 8).then((res) => {
                    // console.log(res.data.data);
                    that.serviceslist = res.data.data.data;
                    that.maxPage = res.data.data.last_page;
                    that.$nextTick(() => {
                        changeLang.zhTran(lang);
                    });
                });
            }
        },
        loadMore () {
            let that = this;
            that.curPage++;
            api.getList(that.curId, that.curPage, lang, 8).then((res) => {
                // console.log(res.data.data);
                that.serviceslist = that.serviceslist.concat(res.data.data.data);
                that.$nextTick(() => {
                    changeLang.zhTran(lang);
                });
            });
        }
    }
};
</script>

<style lang="less" scoped>
    @import '../asset/less/common.less';
    .services{
        width: 100%;
        min-height: 800px;
        background: url(../asset/img/services.jpg) center 0 no-repeat #DFF0F8;
    }
    .services-main{
        position: relative;
        width: @mainWidth;
        margin: 0 auto;
        padding-top: 540px;
    }
    .services-title{
        position: absolute;
        top: 200px;
        width: 280px;
        height: 140px;
        font-size: 48px;
        text-align: right;
        border-bottom: 3px solid #BBD005;
    }
    .line{
        position: absolute;
        top: 337px;
        left: -375px;
        width: 375px;
        height: 3px;
        background-color: #219AD4;
    }
    .services-frame{
        width: 94%;
        min-height: 100px;
        margin: 0 auto;
        padding-bottom: 40px;
        .services-nav{
            width: 1090px;
            height: 50px;
            margin: 0 auto;
            text-align: center;
            line-height: 50px;
            border-bottom: 1px solid #42dfdb;
            li{
                display: inline;
                margin-right: 20px;
                font-size: 16px;
                cursor: pointer;
                &.active, &:hover {
                color: #3da1d5;
                }
            }
        }
        .services-list{
            width: 1090px;
            padding-bottom: 50px;
            &:after{
                .clear;
            }
            .services-item{
                float: left;
                width: 250px;
                margin-top: 50px;
                margin-right: 30px;
                cursor: pointer;
                &:nth-child(4n){
                    margin-right: 0;
                }
                img{
                    width: 100%;
                    height: 188px;
                    border: 1px solid #d7d7d7;
                }
                .item-title{
                    display: block;
                    margin-top: 20px;
                    font-size: 16px;
                    text-align: center;
                    line-height: 20px;
                    color: #061141;
                }
            }
        }
    }
    .btn-more-services{
        width: 200px;
        height: 40px;
        margin: 20px auto 0;
        line-height: 40px;
        color: #219AD4;
        cursor: pointer;
        text-align: center;
        font-size: 18px;
        border-radius: 3px;
        border: 1px solid #219AD4;
        &:hover{
            color: #FFF;
            background-color: #219AD4;
        }
    }
    .services-null{
        position: absolute;
        top: 540px;
        left: 30px;
        width: 94%;
        min-height: 100px;
        font-size: 20px;
        .box-start;
        .box-center;
    }
</style>



