<template>
    <div class="facilities">
        <div class="facilities-main">
            <div class="facilities-title">Facilities<br />專業設施</div>
            <div class="line"></div>
            <div class="facilities-ls ">
                <!-- <div class="facilities-item" v-for="item of testimonialsInfo" :key="item.id">
                    <img class="item-img" :src="item.cover_img">
                    <div class="item-titles">
                        <span v-for="(subItem, index) of item.preface.split(',')" :key="index">{{subItem}}</span>
                    </div>
                    <div class="item-content"></div>
                </div> -->
                <waterfall :line-gap="275" :watch="testimonialsInfo">
                    <waterfall-slot
                        v-for="(item, index) in testimonialsInfo"
                        :width="item.width"
                        :height="item.height"
                        :order="index"
                        :key="item.id"
                    >
                        <div class="facilities-item">
                            <img class="item-img" :src="item.detail.cover_img">
                            <div class="item-titles">
                                <span v-for="(subItem, index) of item.detail.preface.split(',')" :key="index">{{subItem}}</span>
                            </div>
                            <div class="item-content"></div>
                        </div>
                    </waterfall-slot>
                </waterfall>
            </div>
            <div class="facilities-null dn">暂无專業設施</div>
        </div>
    </div>
</template>

<script>
import api from '../api/api';
import Waterfall from 'vue-waterfall/lib/waterfall';
import WaterfallSlot from 'vue-waterfall/lib/waterfall-slot';
import changeLang from '../asset/js/changeLang.js';
let lang = localStorage.getItem('eaestheticLang') || 'zh_hk';

let ItemFactory = (function () {
    let lastIndex = 0;
    function generateRandomItems (data) {
        let items = [];
        let i;
        for (i = 0; i < data.length; i++) {
            items[i] = {
                index: lastIndex++,
                width: 275,
                height: 660 + ~~(Math.random() * 50),
                detail: data[i]
            };
        }
        return items;
    }
    return {
        get: generateRandomItems
    };
})();
export default {
    name: 'facilities',
    components: {
        Waterfall,
        WaterfallSlot
    },
    data () {
        return {
            testimonialsInfo: '',
            curPage: 1,
            maxPage: 1
        };
    },
    created () {
        document.title = '醫美堂-專業設施';
        // 获取专业设施列表
        api.getList(3, this.curPage, lang, 12).then((res) => {
            console.log(res.data.data);
            this.maxPage = res.data.data.last_page;
            this.testimonialsInfo = ItemFactory.get(res.data.data.data);
            this.$nextTick(() => {
                changeLang.zhTran(lang);
            });
        });
        window.addEventListener('scroll', this.onScroll);
    },
    methods: {
        onScroll (event) {
            let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
            if ((scrollTop + window.innerHeight >= (document.body.clientHeight - 50)) && this.maxPage > this.curPage) {
                this.curPage++;
                api.getList(3, this.curPage, lang, 12).then((res) => {
                    // console.log(res.data.data);
                    this.testimonialsInfo = this.testimonialsInfo.concat(ItemFactory.get(res.data.data.data));
                    this.$nextTick(() => {
                        changeLang.zhTran(lang);
                    });
                });
            }
        }
    }
};
</script>

<style lang="less" scoped>
    @import '../asset/less/common.less';
    .facilities{
        width: 100%;
        min-height: 800px;
        background: url(../asset/img/facilities.jpg) center 0 no-repeat #F8F6F7;
    }
    .facilities-main{
        position: relative;
        width: @mainWidth;
        margin: 0 auto;
        padding-top: 540px;
    }
    .facilities-title{
        position: absolute;
        top: 200px;
        width: 280px;
        height: 140px;
        font-size: 48px;
        text-align: right;
        border-bottom: 3px solid #BBD005;
    }
    .line{
        position: absolute;
        top: 337px;
        left: -375px;
        width: 375px;
        height: 3px;
        background-color: #219AD4;
    }
    .facilities-ls{
        width: 94%;
        margin: 0 auto;
        min-height: 100px;
        &:after{
            .clear;
        }
        .facilities-item {
            float: left;
            width: 250px;
            min-height: 500px;
            margin-right: 24px;
            margin-bottom: 30px;
            // padding: 0 20px 20px;
            img.item-img{
                width: 250px;
                height: 525px;
                border: 1px solid #d7d7d7;
                cursor: pointer;
                // background: url("../img/loding.gif") no-repeat center;
            }
            .item-titles{
                width: 90%;
                margin: 0 auto;
                span{
                    width: 100%;
                    height: 26px;
                    margin: 10px auto 0;
                    font-size: 14px;
                    text-align: center;
                    line-height: 26px;
                    background-color: #b3c90b;
                    border-radius: 13px;
                    display: block;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }
            }
            .item-content{
                width: 90%;
                margin: 20px auto;
                padding-bottom: 10px;
                font-size: 14px;
                line-height: 22px;
                border-bottom: 1px solid #d7d7d7;
            }
        }
    }
    .facilities-null{
        position: absolute;
        top: 540px;
        left: 30px;
        width: 94%;
        min-height: 100px;
        font-size: 20px;
        .box-start;
        .box-center;
    }
</style>



