import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);
const store = new Vuex.Store({
    state: {
        isShow: false,
        showBigImg: false,
        imgSrc: '../asset/img/pixel.gif',
        bigImgList: [],
        imgIndex: 0,
        imgLen: 1
    },
    mutations: {
        changeModelState (state) {
            state.isShow = !state.isShow;
        },
        changeBigImg (state, payload) {
            state.showBigImg = !state.showBigImg;
            state.bigImgList = payload.src;
            state.imgSrc = payload.src[payload.index];
            state.imgIndex = payload.index;
            state.imgLen = payload.len;
        },
        changeSrc (state, payload) {
            state.imgIndex = payload.index;
            state.imgSrc = state.bigImgList[payload.index];
        }
    }
});

export default store;



